export default {
	data() {
        return {
			page:1,
			index:null,
			brandId:null,
			startDate: "开始时间",
			endDate:"结束时间",
			array: [{id:"",name:""}],
			noData:'more',
			dataList:[]
        }
	},
	onLoad(){
		this.reqDataList();
		// 获取品牌
		this.$request.POST({
			url:this.$api.apiUrl.POST_QUERY_BRAND,
			data:{
				tags_id:"",
				num:200,
			}
		}).then(res=>{
			if(res.code === 200){
				this.array = res.data;
			}else{
				this.$api.msg(res.msg)
			}
		})
	},
	onReachBottom(){
		if(this.noData === 'noMore'){
			return false;
		}else{
			this.reqDataList();
		}
	},
	methods:{
		// 设置开始时间
		bindStartDateChange: function(e) {
			this.startDate = e.target.value;
			// 如果时间规格不对
			if(!this.computedTime){this.startDate = "开始时间";}
		},
		// 设置结束时间
		bindEndDateChange: function(e) {
			this.endDate = e.target.value;
			// 如果时间规格不对
			if(!this.computedTime){this.endDate = "结束时间";}
		},
		bindPickerChange: function(e) {
			// 设置选中的index
			this.index = e.target.value;
			// 设置品牌id
			this.brandId = this.array[e.target.value].id;
		},
		subimt(){this.reqDataList('btn');},
		reqDataList(e){
			let startTimeTxt,endTimeTxt;
			// 如果时默认值就设置默认请求时间
			if(this.startDate === '开始时间' && this.endDate === '结束时间'){
				startTimeTxt = '';
				endTimeTxt = '';
			}else{
				// 设置携带数据格式
				startTimeTxt = this.startDate.split("-")[0]+ '-' + this.startDate.split("-")[1];
				endTimeTxt = this.endDate.split("-")[0]+ '-' + this.startDate.split("-")[1];
			}
			this.noData = 'loading';
			// 请求页面数据
			this.$request.POST({
				url:this.$api.apiUrl.POST_V4_USER_SALESMANS,
				data:{
					page:this.page,
					row: 10,
					brand_id: this.brandId,
					start:startTimeTxt,
					end: endTimeTxt,
				}
			}).then(res=>{
				if(res.code === 1){
					// 判断还有没有更多的数据
					if(res.data.length < 10){
						this.noData = 'noMore';
					}else{
						this.noData = 'more';
					}
					// 判断是怎么加载
					if(e === 'btn'){
						this.dataList = res.data;
					}else{
						this.dataList = this.dataList.concat(res.data);
					}
				}else{
					this.$api.msg(res.msg);
					this.noData = 'noMore';
				}
			})
		}
	},
	computed:{
		computedTime(){
			// 对比时间;
			let strTimeDate = parseInt(new Date(this.startDate).getTime() / 1000);
			let endTimeDate = parseInt(new Date(this.endDate).getTime() / 1000);
			if(strTimeDate >= endTimeDate){
				this.$api.msg('开始时间不能大于或等于结束时间！');
				return false;
			}else{
				return true;
			}
		}
	}
}
